import './App.css';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import Home from './pages';
import AtomPage from './pages/atom';
import WarpSpeedPage from './pages/warpSpeed';
import SentimentPage from './pages/sentiment';

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/codechemist/" component={Home} exact />
        <Route path="/codechemist/atom" component={AtomPage} exact />
        <Route path="/codechemist/warpSpeed" component={WarpSpeedPage} exact />
        <Route path="/codechemist/sentiment" component={SentimentPage} exact />
      </Switch>
    </Router>
  );
}

export default App;
